















































import { Country } from '@/modules/countries/interfaces/countries.interface';
import { Component, Prop, Vue, Watch } from 'vue-property-decorator';
import { replaceBlankSpaces } from '@/utils/global-functions';

@Component({})
export default class SelectEuropeCountryPopup extends Vue {
    @Prop() showModal!: boolean;
    @Prop() list!: Array<Country>;
    show: boolean = false;
    selectedCountry : Partial<Country> = {};

/* MODAL */
    @Watch('showModal')
     renderModal(){
        this.show = this.showModal;
    }

    @Watch('show')
    sendToParent(newVal: boolean){
        this.$emit('changeModalStatus', newVal)
    }
/* END MODAL */

    selectCountry(country: Country) {
        this.selectedCountry = country;
    }

    submit(){
        this.$emit('selectCountry',this.selectedCountry)
        this.$emit('goToSignUp');
    }

}

