



































































import { Component, Vue } from 'vue-property-decorator';
import InfoSide from '@/components/InfoSide.vue';
import { auth, countries } from '@/store/namespaces';
import AuthMethods from '@/store/auth/methods/auth.methods';
import CountryMethods from '@/store/countries/methods/countries.methods';
import { Country } from '@/modules/countries/interfaces/countries.interface';
import { getSpanishLanCountries, replaceBlankSpaces, showToast } from '@/utils/global-functions';
import Loader from '@/components/utilities/Loader.vue';
import store from '@/store';
import SelectEuropeCountryPopup from '@/modules/auth/components/SelectEuropeCountryPopup.vue';
import {getCountry} from 'country-list-spanish';
import { USER_EMPTY_STATE } from '../../emptyStates/user.emptyState';
import { User } from '../../interfaces/user.interface';

@Component({
    components: {
        InfoSide,
        Loader,
        SelectEuropeCountryPopup
    },
})
export default class Home extends Vue {
    registerButton = {
        name: 'Iniciar sesión',
        text: '¿Ya tienes una cuenta?',
        level: 2,
        redirect: '/inicio-sesion',
    };
    selectedCountry : Partial<Country> = {};
    countries : Country[] = []
    $refs : any ={}
    europeCountriesModal = false;
    europeCountries = []
    europeUnion : Country = {
        id_country: '-1',
        name_country: 'Unión europea',
        country_iso_code: 'eu'
    }
    loader: any;
    isLoading= true;
    fullPage= true
    $loading: any;

    async mounted(){
        this.$refs.loader.showLoader();
        if (await this.fetchResidenceCountries()){
            this.countries = this.residenceCountries;
            this.countries.map(el => el.name_country = getCountry(el.country_iso_code))
            // Fix
            this.europeCountries = this.countries.filter(el => el.country_iso_code === 'ES')
            this.countries = this.countries.filter(el => el.country_iso_code !== 'ES')
            this.countries.push(this.europeUnion)
            this.$refs.loader.hideLoader();
        }else{
                showToast('Ha ocurrido un problema al cargar la página','error')
                this.$router.push({name: 'Login'})
        }
    }

    selectCountry(country: Country) {
        if (country.id_country !== "-1")
            this.selectedCountry = country;
        else 
            this.europeCountriesModal = true;
    }

    /* MODAL */
    changeEuropeCountriesModalStatus(newVal: boolean) {
        this.europeCountriesModal = newVal;
    }
    /* END MODAL */

    goToSignUp() {
        if (this.selectedCountry.id_country) {
            store.state.auth.userSignUp.name_resid_country = this.selectedCountry.country_iso_code
            store.state.auth.userSignUp.id_resid_country = this.selectedCountry.id_country;
            this.$router.push('/registro/datos');
        } else {
            showToast('Seleccione su país de residencia','error')
        }
    }



    @countries.Action(CountryMethods.actions.FETCH_ALL_RESIDENCE_COUNTRIES)
    fetchResidenceCountries!: () => Promise<boolean>
    @countries.Getter(CountryMethods.getters.GET_ALL_RESIDENCE_COUNTRIES)
    residenceCountries!: Country[]
    @auth.Getter(AuthMethods.getters.GET_REGISTER_USER_DATA)
    userData!: Partial<User>;

}
